import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, useNavigate } from 'react-router-dom';
import GoodsList from './components/GoodsList';
import Detail from './Detail';
import Home from './Home';
import Main from './Main';

const GoodsListWrapper = () => {
  const navigate = useNavigate();
  const handleImageClick = (id) => {
    navigate(`/detail/${id}`);
  };

  return <GoodsList onImageClick={handleImageClick} />;
};

const App = () => {
  const [showMain, setShowMain] = useState(false);

  useEffect(() => {
    chrome.storage.local.get(['navigateToMain'], (result) => {
      if (result.navigateToMain) {
        chrome.storage.local.remove('navigateToMain');
        setShowMain(true);
      }
    });
  }, []);

  return (
    <Router>
      <Routes>
        {showMain ? (
          <Route path="/" element={<Main />} />
        ) : (
          <Route path="/" element={<Home />} />
        )}
        <Route path="/goods" element={<GoodsListWrapper />} />
        <Route path="/detail/:id" element={<Detail />} />
      </Routes>
    </Router>
  );
};

export default App;
